'use strict';
const $ = require('jquery');

const formsHandler = require('./modules/formsHandler');

$(document).ready(() => {

    const $linkAuth = $('#authLink');
    const $regLink = $('#regLink');
    const $flipContainer = $('#flip-container');
    // animation handlers start
    const flipStart = () => {
        $flipContainer.removeClass('flip_rotate-2');
        $flipContainer.removeClass('flip_rotate-1');
        $linkAuth
        .parent()
        .addClass('nav-list__item_active')
        .siblings()
        .removeClass('nav-list__item_active');
    };

    const flipStage1 = () => {
        $flipContainer.addClass('flip_rotate-1');
        $regLink
        .parent()
        .addClass('nav-list__item_active')
        .siblings()
        .removeClass('nav-list__item_active');
    };

    const flipStage2 = () => {
        $flipContainer.removeClass('flip_rotate-1');
        $flipContainer.addClass('flip_rotate-2');
    };
    // animation handlers end
    // refirect handler
    const redirect = () => {
        window.location.href = '/edms';
    };
    // handler of flipper switch
    $('#authLink, #regLink').on('click', (e) => {
        e.preventDefault();
        if (e.target === $regLink[0]) {
            flipStage1();
        }
        if (e.target === $linkAuth[0]) {
            flipStart();
        }
    });
    // handler of auth and registration forms
    formsHandler({
        flipStart,
        flipStage1,
        flipStage2,
        redirect,
    });
});


