'use strict';
const $ = require('jquery');
const ajaxSend = require('./ajaxSend');

module.exports = (handlers) => {
    let regData = '';
    $('#sign-in, #registration-stage-1, #registration-stage-2').on('submit', e => {
        e.preventDefault();
        e.stopPropagation();
        const form = $(e.target);
        const data = form.serialize();
    
        if (e.target === $('#sign-in')[0]) {
            ajaxSend(form, data, '/sign-in', null, handlers.redirect);
        }

        if (e.target === $('#registration-stage-1')[0]) {
            regData += data;
            handlers.flipStage2();
        }

        if (e.target === $('#registration-stage-2')[0]) {
            // todo ajax reg user
            regData += '&' + data;
            const clearAlert = () => form.find('.form-block__alert').text('');
            ajaxSend(form, regData, '/registration', handlers.flipStart, clearAlert);
            regData = '';
        }
        e.target.reset();
    });
};



